export default "<template>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar date mode</p>\n    <ev-date-picker\n      v-model=\"date1\"\n      placeholder=\"Select a date.\"\n      clearable\n    />\n    <ev-date-picker\n      v-model=\"date1\"\n      placeholder=\"Select a date.\"\n      disabled\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        date1\n      </span>\n      {{ date1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateTime mode</p>\n    <ev-date-picker\n      v-model=\"dateTime1\"\n      mode=\"dateTime\"\n      :options=\"{\n        timeFormat: 'HH:mm:00',\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        dateTime1\n      </span>\n      {{ dateTime1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateTime mode(shortcuts)</p>\n    <ev-date-picker\n        v-model=\"dateTime2\"\n        mode=\"dateTime\"\n        clearable\n        :options=\"{\n          timeFormat: 'HH:00:ss'\n        }\"\n        :shortcuts=\"[{\n          label: 'Yesterday',\n          value: 'yesterday',\n          shortcutDate: () => new Date(new Date().setDate(new Date().getDate() - 1))\n        }, {\n          label: 'Today',\n          value: 'today',\n          shortcutDate: () => new Date()\n        }]\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateTime2 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateMulti mode(multiType: date, multiDayLimit: 3)</p>\n    <ev-date-picker\n      v-model=\"dateMulti1\"\n      mode=\"dateMulti\"\n      :clearable=\"true\"\n      :options=\"{\n        multiType: 'date',\n        multiDayLimit: 3,\n        disabledDate: (time) => time.getDay() === 0 || time.getDay() === 6\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateMulti1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateMulti mode(multiType: weekday)</p>\n    <ev-date-picker\n      v-model=\"dateMulti2\"\n      mode=\"dateMulti\"\n      :clearable=\"true\"\n      :options=\"{\n        multiType: 'weekday'\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateMulti2 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateMulti mode(multiType: week)</p>\n    <ev-date-picker\n      v-model=\"dateMulti3\"\n      mode=\"dateMulti\"\n      :clearable=\"true\"\n      :options=\"{\n        multiType: 'week',\n        tagShorten: true\n      }\"\n    />\n    <ev-date-picker\n      v-model=\"dateMulti3\"\n      mode=\"dateMulti\"\n      :options=\"{\n        multiType: 'week'\n      }\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateMulti3 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateRange mode</p>\n    <ev-date-picker\n      v-model=\"dateRange1\"\n      mode=\"dateRange\"\n      clearable\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateRange1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateTimeRange mode</p>\n    <ev-date-picker\n        v-model=\"dateTimeRange1\"\n        mode=\"dateTimeRange\"\n        clearable\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateTimeRange1 }}\n    </div>\n  </div>\n  <div class=\"case\">\n    <p class=\"case-title\">Calendar dateTimeRange mode(shortcuts)</p>\n    <ev-date-picker\n        v-model=\"dateTimeRange2\"\n        mode=\"dateTimeRange\"\n        clearable\n        :options=\"{\n          timeFormat: ['HH:00:ss', 'HH:59:ss']\n        }\"\n        :shortcuts=\"dateTimeRange2Shortcut\"\n    />\n    <div class=\"description\">\n      <span class=\"badge\">\n        Value\n      </span>\n      {{ dateTimeRange2 }}\n    </div>\n  </div>\n</template>\n\n<script>\nimport { ref } from 'vue';\nimport { cloneDeep } from 'lodash-es';\n\nexport default {\n  setup() {\n    const date1 = ref('2020-09-01');\n    const dateTime1 = ref('2020-10-15 13:09:10');\n    const dateTime2 = ref('2021-11-22 13:09:10');\n    const dateMulti1 = ref([]);\n    const dateMulti2 = ref([]);\n    const dateMulti3 = ref([]);\n    const dateRange1 = ref([]);\n    const dateTimeRange1 = ref([]);\n    const dateTimeRange2 = ref(['2021-11-10 16:01:01', '2021-12-10 17:10:15']);\n\n    const currentDate = new Date();\n    currentDate.setHours(0);\n    currentDate.setMinutes(0);\n    currentDate.setSeconds(0);\n\n    const dateTimeRange2Shortcut = [\n        {\n          label: 'LastMonth',\n          value: 'lastMonth',\n          shortcutDate: () => [\n            new Date(cloneDeep(currentDate).setDate(currentDate.getDate() - 30)),\n            currentDate,\n          ],\n      },\n      {\n        label: 'LastWeek',\n        value: 'lastWeek',\n        shortcutDate: () => [\n          new Date(cloneDeep(currentDate).setDate(currentDate.getDate() - 6)),\n          currentDate,\n        ],\n      },\n      {\n        label: 'Weekday',\n        value: 'weekday',\n        shortcutDate: () => [\n          new Date(cloneDeep(currentDate)\n              .setDate(currentDate.getDate() - currentDate.getDay() + 1)),\n          new Date(cloneDeep(currentDate)\n              .setDate(currentDate.getDate() + (5 - currentDate.getDay()))),\n        ],\n      },\n      {\n        label: 'Today',\n        value: 'today',\n        shortcutDate: () => [currentDate, currentDate],\n      },\n    ];\n\n    return {\n      date1,\n      dateTime1,\n      dateTime2,\n      dateMulti1,\n      dateMulti2,\n      dateMulti3,\n      dateRange1,\n      dateTimeRange1,\n      dateTimeRange2,\n      dateTimeRange2Shortcut,\n    };\n  },\n};\n</script>\n\n<style>\n.case-block {\n  border: 1px solid black;\n}\n</style>\n";